import React, { useState } from "react";

interface LinkIconProps {
  stepId: string;
  className?: string;
  style?: React.CSSProperties;
}

const LinkIcon: React.FC<LinkIconProps> = ({ stepId, className, style }) => {
  const [copied, setCopied] = useState(false);

  const generateUrl = () => {
    const baseUrl = window.location.origin + window.location.pathname;

    // For the first step (welcome), don't add a slide parameter
    if (stepId === "welcome") {
      return baseUrl;
    }

    return `${baseUrl}?slide=${stepId}`;
  };

  const handleCopyLink = async (e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();

    try {
      const url = generateUrl();
      await navigator.clipboard.writeText(url);

      // Show success feedback
      setCopied(true);
      setTimeout(() => setCopied(false), 1000);
    } catch (err) {
      console.error("Failed to copy URL:", err);
      // Fallback for older browsers
      const textArea = document.createElement("textarea");
      textArea.value = generateUrl();
      document.body.appendChild(textArea);
      textArea.select();
      document.execCommand("copy");
      document.body.removeChild(textArea);

      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    }
  };

  return (
    <button
      onClick={handleCopyLink}
      className={className}
      style={{
        background: "none",
        border: "none",
        outline: "none",
        cursor: "pointer",
        padding: "8px",
        borderRadius: "var(--radius-sm)",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        transition: "all 0.2s ease-in-out",
        color: copied ? "var(--accent-blue)" : "var(--subdued-gray)",
        backgroundColor: copied ? "rgba(0, 207, 232, 0.1)" : "transparent",
        ...style,
      }}
      title={copied ? "Link copied!" : "Copy link to this section"}
      onMouseEnter={(e) => {
        if (!copied) {
          e.currentTarget.style.color = "var(--accent-blue)";
          e.currentTarget.style.backgroundColor = "rgba(0, 207, 232, 0.05)";
        }
      }}
      onMouseLeave={(e) => {
        if (!copied) {
          e.currentTarget.style.color = "var(--subdued-gray)";
          e.currentTarget.style.backgroundColor = "transparent";
        }
      }}
    >
      {copied ? (
        // Clipboard icon when copied
        <svg
          width="16"
          height="16"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="round"
        >
          <rect x="8" y="2" width="8" height="4" rx="1" ry="1" />
          <path d="M16 4h2a2 2 0 0 1 2 2v14a2 2 0 0 1-2 2H6a2 2 0 0 1-2-2V6a2 2 0 0 1 2-2h2" />
        </svg>
      ) : (
        // Link icon
        <svg
          width="16"
          height="16"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="round"
        >
          <path d="M10 13a5 5 0 0 0 7.54.54l3-3a5 5 0 0 0-7.07-7.07l-1.72 1.71" />
          <path d="M14 11a5 5 0 0 0-7.54-.54l-3 3a5 5 0 0 0 7.07 7.07l1.71-1.71" />
        </svg>
      )}
    </button>
  );
};

export default LinkIcon;
