import React from "react";
import { ControlsPanelProps } from "../../types";
import { formatClockTime } from "../../utils/calculations";
import { useMediaQuery } from "../../hooks/useMediaQuery";

const ControlsPanel: React.FC<ControlsPanelProps> = ({
  thresholdSetting,
  onThresholdChange,
  onResetSimulation,
  watermarkDelay,
  onwatermarkDelayChange,
  rate,
  onRateChange,
  producerStates,
  onProducerToggle,
  isPaused,
  onPauseSet,
  hideProducers,
  onHideProducerSet,
  hideWindows,
  onHideWindowsSet,
  spotlightProducer,
  onSpotlightProducerSet,
  onHighlightClosestParticle,
  onSetWindowSizeTo100000,
  elapsedTime,
  windowWidth,
  showControlsPanel,
  onControlsPanelSet,
}) => {
  // Media query hook for responsive behavior
  const { isPortrait, isMobile, isTablet } = useMediaQuery();

  // Hide controls panel completely in portrait mobile
  if (isPortrait && (isMobile || isTablet)) {
    return null;
  }

  // Handler for highlight button that pauses simulation if needed
  // const handleHighlightClick = () => {
  //   if (!isPaused) {
  //     onPauseSet(true); // Set to paused
  //   }
  //   onHighlightClosestParticle();
  // };

  const maxWatermark = 15000;
  const minWatermark = 500;

  return (
    <div style={{ position: "relative", width: "100%", height: "100%" }}>
      {/* Time at Producer - Always visible */}
      <div
        style={{
          position: "absolute",
          top: "var(--spacing-sm)",
          left: "var(--spacing-sm)",
          background: "var(--background-white)",
          color: "var(--text-gray)",
          padding: "var(--spacing-sm)",
          borderRadius: "var(--radius-md)",
          fontFamily: "var(--font-family)",
          zIndex: 10,
          boxShadow: "var(--shadow-soft)",
          border: "1px solid rgba(115, 103, 240, 0.1)",
        }}
      >
        <div style={{ marginBottom: "var(--spacing-sm)" }}>
          <label
            style={{
              fontSize: "0.75rem",
              fontWeight: "600",
              color: "var(--subdued-gray)",
            }}
          >
            Time at Producer
          </label>
        </div>
        <div
          style={{
            fontSize: windowWidth <= 600 ? "0.875rem" : "1rem",
            fontWeight: "600",
            color: "var(--accent-purple)",
            fontFamily: "monospace",
          }}
        >
          {formatClockTime(elapsedTime)}
        </div>
      </div>

      {/* Controls Toggle Button */}
      <button
        className={showControlsPanel ? "btn-secondary" : "btn-primary"}
        onClick={() => onControlsPanelSet(!showControlsPanel)}
        style={{
          position: "absolute",
          top: "var(--spacing-sm)",
          left: windowWidth <= 600 ? "180px" : "220px",
          padding: "8px 16px",
          fontSize: "0.875rem",
          zIndex: 1000,
        }}
      >
        {showControlsPanel ? "Hide Controls" : "Show Controls"}
      </button>

      {/* Controls Panel - Full width, flush with top, semi-transparent background */}
      {showControlsPanel && (
        <div
          style={{
            position: "absolute",
            top: 0,
            left: 0,
            width: "100%",
            background: "rgba(255, 255, 255, 0.75)",
            backdropFilter: "blur(10px)",
            borderBottom: "1px solid rgba(0, 0, 0, 0.1)",
            padding: "var(--spacing-sm)",
            zIndex: 100,
            display: "flex",
            flexWrap: "wrap",
            gap: "var(--spacing-lg)",
            alignItems: "flex-start",
            paddingTop: "80px", // Space for the time display and toggle button
          }}
        >
          {/* Window Size Slider */}
          <div
            style={{
              background: "var(--background-white)",
              padding: "var(--spacing-sm)",
              borderRadius: "var(--radius-md)",
              boxShadow: "var(--shadow-soft)",
              border: "1px solid rgba(115, 103, 240, 0.1)",
              minWidth: windowWidth <= 600 ? "160px" : "200px",
              flex: "0 0 auto",
            }}
          >
            <div style={{ marginBottom: "var(--spacing-sm)" }}>
              <label
                htmlFor="windowSize"
                style={{
                  fontSize: "0.75rem",
                  fontWeight: "600",
                  color: "var(--subdued-gray)",
                  display: "block",
                  marginBottom: "var(--spacing-xs)",
                }}
              >
                Window Size
              </label>
              <div
                style={{
                  fontSize: "0.875rem",
                  fontWeight: "600",
                  color: "var(--accent-purple)",
                  fontFamily: "monospace",
                }}
              >
                {thresholdSetting}ms
              </div>
            </div>
            <input
              id="windowSize"
              type="range"
              min="500"
              max="5000"
              step="100"
              value={thresholdSetting}
              onChange={(e) => onThresholdChange(Number(e.target.value))}
              onMouseDown={(e) => {
                (e.currentTarget as any)._initialValue = Number(
                  e.currentTarget.value
                );
              }}
              onMouseUp={(e) => {
                const newValue = Number(e.currentTarget.value);
                const initialValue = (e.currentTarget as any)._initialValue;
                if (newValue !== initialValue) {
                  onResetSimulation();
                }
              }}
              onKeyUp={(e) => {
                if (e.key === "Enter" || e.key === " ") {
                  const newValue = Number(e.currentTarget.value);
                  const initialValue =
                    (e.currentTarget as any)._initialValue || thresholdSetting;
                  if (newValue !== initialValue) {
                    onResetSimulation();
                  }
                }
              }}
              style={{
                width: "100%",
                accentColor: "var(--accent-purple)",
              }}
            />
          </div>

          {/* Watermark Delay Slider */}
          <div
            style={{
              background: "var(--background-white)",
              padding: "var(--spacing-sm)",
              borderRadius: "var(--radius-md)",
              boxShadow: "var(--shadow-soft)",
              border: "1px solid rgba(115, 103, 240, 0.1)",
              minWidth: windowWidth <= 600 ? "160px" : "200px",
              flex: "0 0 auto",
            }}
          >
            <div style={{ marginBottom: "var(--spacing-sm)" }}>
              <label
                htmlFor="watermarkDelay"
                style={{
                  fontSize: "0.75rem",
                  fontWeight: "600",
                  color: "var(--subdued-gray)",
                  display: "block",
                  marginBottom: "var(--spacing-xs)",
                }}
              >
                Watermark Delay
              </label>
              <div
                style={{
                  fontSize: "0.875rem",
                  fontWeight: "600",
                  color: "var(--accent-blue)",
                  fontFamily: "monospace",
                }}
              >
                {watermarkDelay}ms
              </div>
            </div>
            <input
              id="watermarkDelay"
              type="range"
              min={"0"}
              max={maxWatermark}
              step={minWatermark}
              value={watermarkDelay}
              onChange={(e) => onwatermarkDelayChange(Number(e.target.value))}
              onMouseDown={(e) => {
                (e.currentTarget as any)._initialValue = Number(
                  e.currentTarget.value
                );
              }}
              onMouseUp={(e) => {
                const newValue = Number(e.currentTarget.value);
                const initialValue = (e.currentTarget as any)._initialValue;
                if (newValue !== initialValue) {
                  onResetSimulation();
                }
              }}
              onKeyUp={(e) => {
                if (e.key === "Enter" || e.key === " ") {
                  const newValue = Number(e.currentTarget.value);
                  const initialValue =
                    (e.currentTarget as any)._initialValue || watermarkDelay;
                  if (newValue !== initialValue) {
                    onResetSimulation();
                  }
                }
              }}
              style={{
                width: "100%",
                accentColor: "var(--accent-blue)",
              }}
            />
          </div>

          {/* Rate Slider */}
          <div
            style={{
              background: "var(--background-white)",
              padding: "var(--spacing-sm)",
              borderRadius: "var(--radius-md)",
              boxShadow: "var(--shadow-soft)",
              border: "1px solid rgba(115, 103, 240, 0.1)",
              minWidth: windowWidth <= 600 ? "160px" : "200px",
              flex: "0 0 auto",
            }}
          >
            <div style={{ marginBottom: "var(--spacing-sm)" }}>
              <label
                htmlFor="rate"
                style={{
                  fontSize: "0.75rem",
                  fontWeight: "600",
                  color: "var(--subdued-gray)",
                  display: "block",
                  marginBottom: "var(--spacing-xs)",
                }}
              >
                Rate
              </label>
              <div
                style={{
                  fontSize: "0.875rem",
                  fontWeight: "600",
                  color: "var(--accent-green)",
                  fontFamily: "monospace",
                }}
              >
                {rate}x
              </div>
            </div>
            <input
              id="rate"
              type="range"
              min="1"
              max="10"
              step="1"
              value={rate}
              onChange={(e) => onRateChange(Number(e.target.value))}
              style={{
                width: "100%",
                accentColor: "var(--accent-green)",
              }}
            />
          </div>

          {/* Producer Toggle Buttons */}
          <div
            style={{
              background: "var(--background-white)",
              padding: "var(--spacing-sm)",
              borderRadius: "var(--radius-md)",
              boxShadow: "var(--shadow-soft)",
              border: "1px solid rgba(115, 103, 240, 0.1)",
              flex: "0 0 auto",
            }}
          >
            <div style={{ marginBottom: "var(--spacing-sm)" }}>
              <label
                style={{
                  fontSize: "0.75rem",
                  fontWeight: "600",
                  color: "var(--subdued-gray)",
                  display: "block",
                }}
              >
                Producers
              </label>
            </div>
            <div
              style={{
                display: "flex",
                gap: "var(--spacing-xs)",
                flexWrap: "wrap",
              }}
            >
              {[0, 1, 2].map((index) => (
                <button
                  key={index}
                  onClick={() => {
                    const currentState = producerStates[index];
                    const desiredState = !currentState;
                    if (currentState !== desiredState) {
                      onProducerToggle(index, desiredState);
                    }
                    if (desiredState && hideProducers[index]) {
                      onHideProducerSet(index, false);
                    }
                  }}
                  className={
                    producerStates[index] ? "btn-primary" : "btn-secondary"
                  }
                  style={{
                    padding: "6px 12px",
                    fontSize: "0.75rem",
                    fontWeight: "600",
                    minWidth: "60px",
                  }}
                >
                  P{index + 1}: {producerStates[index] ? "ON" : "OFF"}
                </button>
              ))}
            </div>
          </div>

          {/* Action Buttons */}
          <div
            style={{
              background: "var(--background-white)",
              padding: "var(--spacing-sm)",
              borderRadius: "var(--radius-md)",
              boxShadow: "var(--shadow-soft)",
              border: "1px solid rgba(115, 103, 240, 0.1)",
              flex: "0 0 auto",
            }}
          >
            <div style={{ marginBottom: "var(--spacing-sm)" }}>
              <label
                style={{
                  fontSize: "0.75rem",
                  fontWeight: "600",
                  color: "var(--subdued-gray)",
                  display: "block",
                }}
              >
                Actions
              </label>
            </div>
            <div
              style={{
                display: "flex",
                gap: "var(--spacing-xs)",
                flexWrap: "wrap",
              }}
            >
              <button
                onClick={() => {
                  const currentState = isPaused;
                  const desiredState = !currentState;
                  if (currentState !== desiredState) {
                    onPauseSet(desiredState);
                  }
                }}
                className={isPaused ? "btn-secondary" : "btn-primary"}
                style={{
                  padding: "6px 12px",
                  fontSize: "0.75rem",
                  fontWeight: "600",
                  minWidth: "60px",
                }}
              >
                {isPaused ? "Play" : "Pause"}
              </button>
              <button
                onClick={() => {
                  const currentState = hideWindows;
                  const desiredState = !currentState;
                  if (currentState !== desiredState) {
                    onHideWindowsSet(desiredState);
                  }
                }}
                className={hideWindows ? "btn-secondary" : "btn-primary"}
                style={{
                  padding: "6px 12px",
                  fontSize: "0.75rem",
                  fontWeight: "600",
                  minWidth: "60px",
                }}
              >
                {hideWindows ? "Show Windows" : "Hide Windows"}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ControlsPanel;
