import { ScrollStep } from "../types";

// This will be populated by the useScrollytelling hook
let steps: ScrollStep[] = [];

export const setSteps = (newSteps: ScrollStep[]) => {
  steps = newSteps;
};

export const getSteps = (): ScrollStep[] => {
  return steps;
};

export const getStepIds = (): string[] => {
  return steps.map((step) => step.id);
};

export const getStepIdFromSlideNumber = (slideNumber: number): string => {
  const stepIds = getStepIds();
  return stepIds[slideNumber - 1] || "welcome";
};

export const getSlideNumberFromStepId = (stepId: string): number => {
  const stepIds = getStepIds();
  const index = stepIds.indexOf(stepId);
  return index === -1 ? -1 : index + 1;
};
