import React from "react";
import MarkdownRenderer from "./MarkdownRenderer";

interface MarkdownOverlayProps {
  isVisible: boolean;
  content: string;
  onClose: () => void;
  title?: string;
}

const MarkdownOverlay: React.FC<MarkdownOverlayProps> = ({
  isVisible,
  content,
  onClose,
  title = "Information",
}) => {
  if (!isVisible) return null;

  return (
    <div
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: "rgba(0, 0, 0, 0.6)",
        zIndex: 1000,
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        padding: "var(--spacing-sm)",
        backdropFilter: "blur(4px)",
      }}
      onClick={onClose}
    >
      <div
        className="card"
        style={{
          maxWidth: "800px",
          maxHeight: "80vh",
          width: "100%",
          overflow: "hidden",
          display: "flex",
          flexDirection: "column",
          boxShadow: "var(--shadow-strong)",
          border: "1px solid rgba(115, 103, 240, 0.1)",
        }}
        onClick={(e) => e.stopPropagation()}
      >
        {/* Header */}
        <div
          style={{
            padding: "var(--spacing-lg)",
            borderBottom: "1px solid rgba(115, 103, 240, 0.1)",
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            background: "linear-gradient(135deg, rgba(115, 103, 240, 0.3) 0%, rgba(0, 207, 232, 0.3) 100%)",
            color: "var(--accent-purple)",
          }}
        >
          <h2
            style={{
              margin: 0,
              fontSize: "1.5rem",
              fontWeight: "600",
              letterSpacing: "-0.01em",
            }}
          >
            {title}
          </h2>
          <button
            onClick={onClose}
            style={{
              background: "rgba(255, 255, 255, 0.2)",
              border: "none",
              fontSize: "20px",
              color: "var(--accent-purple)",
              cursor: "pointer",
              padding: "var(--spacing-xs)",
              borderRadius: "var(--radius-sm)",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              width: "32px",
              height: "32px",
              transition: "all 0.3s ease",
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.backgroundColor =
                "rgba(255, 255, 255, 0.3)";
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.backgroundColor =
                "rgba(255, 255, 255, 0.2)";
            }}
            aria-label="Close overlay"
          >
            ×
          </button>
        </div>

        {/* Content */}
        <div
          style={{
            padding: "var(--spacing-xl)",
            overflowY: "auto",
            flex: 1,
            textAlign: "left",
            color: "var(--text-gray)",
            lineHeight: "1.6",
          }}
        >
          <MarkdownRenderer content={content} />
        </div>

        {/* Footer */}
        <div
          style={{
            padding: "var(--spacing-md) var(--spacing-xl)",
            borderTop: "1px solid rgba(115, 103, 240, 0.1)",
            background: "var(--light-gray-bg)",
            display: "flex",
            justifyContent: "flex-end",
          }}
        >
          <button
            className="btn-primary"
            onClick={onClose}
            style={{
              fontSize: "0.875rem",
              padding: "10px 20px",
            }}
          >
            Close
          </button>
        </div>
      </div>
    </div>
  );
};

export default MarkdownOverlay;
