import React from "react";
import { ScrollControllerProps } from "../../types";
import { useScrollytellingNew } from "../../hooks/useScrollytellingNew";
import { useMediaQuery } from "../../hooks/useMediaQuery";
import MarkdownRenderer from "../ui/MarkdownRenderer";
import LinkIcon from "../ui/LinkIcon";

const ScrollController: React.FC<ScrollControllerProps> = (props) => {
  const { containerRef, steps, activeStepId } = useScrollytellingNew(props);
  const { isLandscapeMobile } = useMediaQuery();

  // Responsive padding - reduce margin in landscape mobile
  const getResponsivePadding = () => {
    if (isLandscapeMobile) {
      return "var(--spacing-sm) var(--spacing-md)";
    }
    return "var(--spacing-xl)";
  };

  return (
    <div
      ref={containerRef}
      style={{
        height: "100%",
        overflowY: "auto",
        padding: getResponsivePadding(),
        backgroundColor: "var(--light-gray-bg)",
        fontFamily: "var(--font-family)",
        position: "relative",
      }}
    >
      {/* Gradient background wave */}
      <div
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          right: 0,
          height: "200px",
          background:
            "linear-gradient(135deg, rgba(115, 103, 240, 0.02) 0%, rgba(0, 207, 232, 0.02) 100%)",
          zIndex: 0,
        }}
      />

      <div style={{ position: "relative", zIndex: 1 }}>
        <h1
          className="headline"
          style={{
            marginBottom: "var(--spacing-2xl)",
            color: "var(--dark-text)",
            fontSize: "2.75rem",
            fontWeight: "700",
            letterSpacing: "-0.02em",
            lineHeight: "1.1",
          }}
        >
          flink-watermarks.
          <em style={{ color: "var(--accent-purple)", fontStyle: "italic" }}>
            wtf
          </em>
        </h1>

        {steps.map((step, index) => (
          <div
            key={step.id}
            id={step.id}
            className={`scroll-step ${
              activeStepId === step.id ? "card-active" : "card"
            }`}
            style={{
              marginBottom: "var(--spacing-xl)",
              minHeight: "200px",
              textAlign: "left",
              transition: "all 0.3s ease-in-out",
              position: "relative",
            }}
          >
            {/* Top bar with counter and link icon */}
            <div
              style={{
                position: "absolute",
                top: "var(--spacing-sm)",
                right: "var(--spacing-sm)",
                zIndex: 10,
                display: "flex",
                alignItems: "center",
                gap: "var(--spacing-xs)",
              }}
            >
              {/* Card counter */}
              <div
                style={{
                  fontSize: "0.75rem",
                  color: "var(--subdued-gray)",
                  fontWeight: "500",
                  fontFamily: "monospace",
                  backgroundColor: "rgba(255, 255, 255, 0.4)",
                  padding: "2px 4px",
                  borderRadius: "1px",
                  border: "1px solid rgba(0, 0, 0, 0.05)",
                  opacity: 0.7,
                }}
              >
                {index + 1} / {steps.length}
              </div>

              {/* Link icon */}
              <div style={{ opacity: 0.7 }}>
                <LinkIcon stepId={step.id} />
              </div>
            </div>

            <h2
              style={{
                color:
                  activeStepId === step.id
                    ? "var(--accent-purple)"
                    : "var(--accent-blue)",
                marginBottom: "var(--spacing-md)",
                marginTop: "var(--spacing-xl)", // Add top padding below the top bar
                fontSize: "1.5rem",
                fontWeight: "600",
                letterSpacing: "-0.01em",
                lineHeight: "1.3",
                paddingRight: "80px", // Add padding to prevent overlap with top bar
              }}
            >
              {step.title}
            </h2>
            <div style={{ color: "var(--text-gray)", lineHeight: "1.6" }}>
              <MarkdownRenderer content={step.content} />
            </div>
            {(step.overlayContent || (step as any).overlayButtonText) && (
              <div
                style={{ marginTop: "var(--spacing-lg)", textAlign: "left" }}
              >
                <button
                  className="btn-secondary"
                  onClick={() => {
                    if (step.overlayContent) {
                      props.onShowOverlay(
                        step.overlayContent,
                        (step as any).overlayTitle,
                        step.id
                      );
                    } else {
                      props.onReopenOverlay();
                    }
                  }}
                  style={{
                    fontSize: "0.875rem",
                    padding: "10px 20px",
                  }}
                >
                  {(step as any).overlayButtonText ||
                    "Open Further Explanation"}
                </button>
              </div>
            )}
          </div>
        ))}

        {/* Spacer div to allow last step to trigger properly */}
        <div
          style={{
            height: "50vh",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            opacity: 0.3,
          }}
        />
      </div>
    </div>
  );
};

export default ScrollController;
